<script>
import { envs } from '@/utils/envs';
import history from '@/utils/history';
import Scrollable from '@/components/Scrollable.vue';
import { stringifyUrl } from 'query-string';

export default {
  components: {
    Scrollable,
  },
  data() {
    return {
      activeName: ['1', '2', '3', '4', '5', '7'],
    };
  },
  // mounted() {
  //   console.log(this.$can('', '6076', false));
  // },
  computed: {
    showStock() {
      return ['6030', '6032', '6033', '6036'].every(i => {
        return this.$can('', i, false);
      });
    },
  },
  methods: {
    isContain(/** @type {Element[]} * */elements, /** @type {Element} * */el) {
      return elements.some(elem => {
        return elem.contains(el) || elem === el;
      });
    },
    handleIn(e) {
      this.inElem = e.target;
      this.toggle();
    },
    handleOut() {
      this.inElem = undefined;
      this.toggle();
    },

    generateURL2MaterialAdmin({ path, query = null }) {
      let domain;
      if (envs.VUE_APP_IS_REMOTE) {
        domain = `${globalThis.location.protocol}//${globalThis.location.host}`;
      } else {
        domain = '//localhost:9220';
      }
      // const domain = `${globalThis.location.protocol}//${globalThis.location.host}`;
      const materialAdminBase = '/material-admin';
      const url = `${domain}${materialAdminBase}${path}`;
      const res = stringifyUrl({ url, query });
      // console.log(res);
      return res;
    },
    openMaterialAdmin({ path, query = null }) {
      // this.$router.push({
      //   path: `/material-admin${path}`,
      //   query,
      // });
      if (envs.VUE_APP_IS_REMOTE) {
        // window.open(`/material-admin${path}${query ? `?status=${query.status}` : ''}`);
        history.push({
          path: `/material-admin${path}`,
          query,
        });
      } else {
        // window.open(`//localhost:9220/material-admin${path}${query ? `?status=${query.status}` : ''}`);
        window.location.href = `//localhost:9220/material-admin${path}${query ? `?status=${query.status}` : ''}`;
      }
    },
  },
};
</script>

