<template>
  <aside class="file-aside" :class="{ 'hide-icon': hideIcon }">
    <div class="aside-container">
      <!-- 我的鞋款 -->
      <router-link
        class="aside-item"
        :class="{ disabled: !can[110], 'router-link-active': appType === 11 }"
        v-if="showMyDesign"
        :event="!can[110] ? '' : 'click'"
        :to="{ name: 'FileList', query: { appType: 11 } }"
        exact
      >
        <i class="iconfont icon-cube"></i>
        <p class="aside-title">{{ $t("sidebar.my_design") }}</p>
      </router-link>

      <!-- 我的面料 -->
      <router-link
        class="aside-item"
        :class="{ disabled: !can[111], 'router-link-active': appType === 12 }"
        v-if="showMyMaterial"
        :event="!can[111] ? '' : 'click'"
        :to="{ name: 'FileList', query: { appType: 12 } }"
        exact
      >
        <i class="iconfont icon-material"></i>
        <p class="aside-title">{{ $t("sidebar.my_materials") }}</p>
      </router-link>

      <!-- 我的协作空间 -->
      <router-link
        v-if="(!isIndependentDesigner && !isVirtualAccount) || (isVirtualAccount && isVirtualAccountShowWorkspace)"
        class="aside-item"
        :class="{ disabled: !can[112], 'router-link-active': appType === 13 }"
        :event="!can[112] ? '' : 'click'"
        :to="{ name: 'FileList', query: { appType: 13 } }"
        exact
      >
        <i class="iconfont icon-tool"></i>
        <p class="aside-title">{{ $t("sidebar.workspace") }}</p>
      </router-link>

      <!-- 订货会  -->
      <!-- <router-link
        v-if="!isIndependentDesigner && user.tradeFairWorkspace && !isBusinessType1 && !isAntaDomain"
        class="aside-item"
        :to="{path:'/order-meeting/index', query: {workspaceId:user.tradeFairWorkspace}}"
      >
        <i class="iconfont icondinghuohui"></i>
        <p class="aside-title">{{$t('sidebar.order_meeting')}}</p>
      </router-link> -->
      <!-- 面料库 -- 面料商可进入 -->
      <router-link
        v-if="
          !isIndependentDesigner && isMaterialSupplier && $can('', 158) && !isBusinessType1 && !isCloth && !isAntaDomain
        "
        class="aside-item"
        :to="{ name: 'FileList', query: { appType: 16 } }"
        exact
        :class="{ 'router-link-active': appType === 16 }"
      >
        <i class="iconfont iconmianliaoku_nol"></i>
        <p class="aside-title">{{ $t("sidebar.material_library") }}</p>
      </router-link>

      <!-- 商家后台 -- 面料商可进入 -->
      <router-link
        v-if="
          !isIndependentDesigner && isMaterialSupplier && $can('', 158) && !isBusinessType1 && !isCloth && !isAntaDomain
        "
        class="aside-item"
        exact
        :to="`/supplier-backend/goods-manager?companyId=${user.companyId}`"
      >
        <i class="iconfont iconhoutai_nol"></i>
        <p class="aside-title">{{ $t("sidebar.supplier") }}</p>
      </router-link>
      <!-- 安踏版商家后台, 仅打开材料管理后台 -->
      <div
        v-if="!isIndependentDesigner && isAbleToViewMaterialAdmin"
        class="aside-item"
        @click="goToMaterialAdmin"
        @mouseenter="handleIn"
        @mouseleave="handleOut"
      >
        <i class="iconfont iconcailiaokuhoutai1"></i>
        <p class="aside-title">{{ $t("sidebar.anta_supplier") }}</p>
      </div>
      <!-- 面料商城 -- 品牌商或设计师可进入 -->

      <router-link
        to="/material-mall"
        v-if="(isDesigner || isBrand) && !isAnta && $can('', 158) && !isCloth && !isAntaDomain"
        class="aside-item"
      >
        <i class="iconfont iconcailiaoshangcheng_nol"></i>
        <p class="aside-title">{{ $t("sidebar.material_mart") }}</p>
      </router-link>

      <!-- 订单管理 -- 拥有特定权限可见 -->
      <!-- TODO: 国际化 -->
      <router-link
        :to="{ name: 'orderManagement' }"
        v-if="can[3001] && !isBusinessType1 && !isAntaDomain"
        class="aside-item"
      >
        <i class="iconfont icondingdanguanli"></i>
        <p class="aside-title">
          {{ $t("order_management.table.order_management") }}
        </p>
      </router-link>

      <!-- 上会系统  v-if="$can('', 8001, false) || meetListLength" -->
      <router-link
        v-if="!isAnta && meetListLength !== null"
        :to="{ name: 'upMeetingList' }"
        tag="div"
        class="aside-item"
      >
        <i class="iconfont iconshanghuixitong"></i>
        <p class="aside-title">{{ $t("virtual-meetup.public.name") }}</p>
      </router-link>
    </div>
    <transition name="slide-fade">
      <MaterialSidebar
        class="external-sidebar-container"
        v-show="showExternalSidebar"
        @mouseenter.native="handleIn"
        @mouseleave.native="handleOut"
      />
    </transition>
  </aside>
</template>

<script>
import { debounce } from 'throttle-debounce';
import MaterialSidebar from '@/components/AntaSider.vue';
import axios from '@/utils/axios';
import { fetchMeetList } from '@/views/up-meeting/mlist/index';
import { getContextUser } from '../../../loaders/context';
import { getCurrentCompany } from '../../../loaders/company';
import { envs } from '../../../utils/envs';


export default {
  components: {
    MaterialSidebar,
  },
  props: {
    entryAppType: Number,
  },
  data() {
    this.toggle = debounce(100, () => {
      this.showExternalSidebar = !!this.inElem;
      // this.showExternalSidebar = true;
    });
    return {
      showExternalSidebar: false,
      isVirtualAccountShowWorkspace: false,
      isVirtualAccountShowMeetUp: false,
      meetListLength: null,
    };
  },
  computed: {
    isBusinessType1() {
      return this.user?.businessType === 1;
    },
    isAbleToViewMaterialAdmin() {
      return this.$can('', '6001');
    },
    isIndependentDesigner() {
      return this.user.mail === 'designer_helper@163.com';
    },
    appType() {
      return this.entryAppType;
    },
    can() {
      return {
        110: this.$can('', 110),
        111: this.$can('', 111),
        112: this.$can('', 112),
        3001: this.$can('', 3001, false),
      };
    },
    user() {
      return getContextUser();
    },
    company() {
      return getCurrentCompany();
    },
    isAntaDomain() {
      return envs.VUE_APP_COOPERATION.includes('anta');
    },
    showMyDesign() {
      if (this.isVirtualAccount) {
        if (this.isVirtualAccountShowWorkspace) return true;
        return false;
      }
      return !this.isBusinessType1 && !this.isAntaDomain;
    },
    showMyMaterial() {
      if (this.isVirtualAccount) {
        if (this.isVirtualAccountShowWorkspace) return true;
        return false;
      }
      return !this.isIndependentDesigner && !this.isBusinessType1;
    },
    /**
    ** @return { boolean }
    */
    hideIcon() {
      // return this.isAntaDomain || this.isBusinessType1;
      return this.isAntaDomain;
    },
    /**
    * @return { boolean }
    */
    isAnta() {
      return this.user && (this.user.businessType === 1 || this.user.businessType === 3);
    },
    isCloth() {
      return this.user && this.user.businessType === 2;
    },
    /**
    * @return { boolean }
    */
    isDesigner() {
      return this.company && this.company.type === 1;
    },
    /**
    * @return { boolean }
    */
    isMaterialSupplier() {
      return this.company && this.company.type === 3;
    },
    /**
    * @return { boolean }
    */
    isBrand() {
      return this.company && this.company.type === 5;
    },
    /**
    * @return { boolean }
    */
    isVirtualAccount() {
      return this.user && this.user.roleId === '3';
    },
  },
  methods: {
    goToMaterialAdmin() {
      if (envs.VUE_APP_IS_REMOTE) {
        // window.location.href = '/material-admin/';
        window.open('/material-admin/');
      } else {
        // window.location.href = '//localhost:9220/material-admin/';
        window.open('//localhost:9220/material-admin/');
      }
    },
    showAntaSidebar() {
      console.log('hover event');
    },
    isContain(/** @type {Element[]} * */elements, /** @type {Element} * */el) {
      return elements.some(elem => {
        return elem.contains(el) || elem === el;
      });
    },
    handleIn(e) {
      this.inElem = e.target;
      this.toggle();
    },
    handleOut() {
      this.inElem = undefined;
      this.toggle();
    },
    async setMeetListLength() {
      try {
        this.meetListLength = (await fetchMeetList({ status: '', condition: '' }))?.total || 0;
      } catch {
        this.meetListLength = null;
      }
    },
  },
  created() {
    this.$watch('isVirtualAccount', async (val) => {
      if (val) {
        const res1 = (await axios.post('/authcenter/user/check/batch', {
          userIds: [this.user.id],
        })).data;
        this.isVirtualAccountShowWorkspace = res1[this.user.id];
        const res2 = (await axios.post('/meeting/member/virtual/exists', {
          userIds: [this.user.id],
        })).data;
        this.isVirtualAccountShowMeetUp = res2[this.user.id];
      }
    }, {
      immediate: true,
    });
    this.setMeetListLength();
  },
};
</script>

